.swiper__button-style {
  /*&::after {
    color: var(--color-swiper-button);
    font-size: 30px;
   }*/
  position: absolute;
  top: 50%;
  width: 37px;
  height: 37px;
  z-index: 2;
  padding: 10px;
  cursor: pointer;
}
.swiper__button-style::before {
  content: "";
  position: absolute;
  top: 50%;
  width: 23px;
  height: 23px;
  border-left: 1px solid;
  border-bottom: 1px solid;
  border-color: var(--color-swiper-button);
}
@media (--small-vp) {
  .swiper__button-style::before {
    width: 13px;
    height: 13px;
  }
}
.swiper__button-style--next {
  right: 0;
  transform: translate(2px, -50%);
}
.swiper__button-style--next::before {
  right: 50%;
  transform-origin: top right;
  transform: rotate(225deg) translate(50%, -50%);
}
.swiper__button-style--prev {
  left: 0;
  transform: translate(-2px, -50%);
}
.swiper__button-style--prev::before {
  left: 50%;
  transform-origin: top left;
  transform: rotate(45deg) translate(-50%, -50%);
}
@media (--small-vp) {
  .swiper__button-style {
    width: 20px;
    height: 30px;
    padding: 0;
  }
}

